$main-color : #800000;

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body{
    font-family: 'Roboto', sans-serif;
    color: black;
}

.active-nav{
    color: $main-color;
    border-bottom: 1px solid $main-color;
}

.brand{
    color: $main-color !important;
    font-weight: bolder;
    font-size: 50px;
}

.navbar{
    padding: 0;
}

.page-title{
    color: $main-color;
}

.btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 6px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: $main-color;
    border: 2px solid $main-color;
    text-decoration: none;
}

.btn-get-started:hover{
    background-color: $main-color;
    color: white;
    text-decoration: none;
}

.anchor{
    color: $main-color;
    text-decoration: none;
}

.anchor:hover{
    color: white;
    text-decoration: none;
}

.animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

@keyframes up-down{
    0%{
        transform: translateY(10px);
    }
    100%{
        transform: translateY(-10px);
    }
}

.card-title{
    color: $main-color;
}

footer{
    margin-top: 50px;
}

.site-footer{
    color: $main-color;
}

.site-footer:hover{
    color: $main-color;
    font-weight: bold;
    text-decoration: none;
}